html{
  font-size: 77%;
  box-sizing: border-box;
}
body, #root{
  margin: 0;
  background-color: rgb(173, 170, 159);
  height: 100vh;
  overflow: hidden;
}

:root{
  --main-color: rgb(241, 241, 198);
}
.container{
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  line-height: 1.6;
  text-align: center;
  height: 100%;
}
.logo{
  padding: 1rem 0 0;
  color: var(--main-color);
  text-shadow: -1px 0 rgb(15, 15, 15), 0 1px black, 1px 0 black, 0 -1px rgb(58, 56, 56);
}
.subTitle{
  color: var(--main-color);
  font-style: italic;
}
.slide-container {
  position: relative;
  margin: 3% auto ;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.opacity{
  opacity: .7;
  transition:  .7s ease-in;
}
.image {
  width: 640px;
  height: 380px;
  width: 100%;
  display: block;
  border-radius: 10px; 
  transition:  .8s ease-in-out;
  
}

/* .image:hover {
  transform: scaleX(-1);
} */

.arrow{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  height: 40px;
  width: 40px;
  background-color: var(--main-color);
  border-radius: 50%;
  font-size: 2rem;
  top: 50%;
  line-height: 40px;
  cursor: pointer;
  z-index: 10;
  color: #4b483f;
  text-align: center;
  transition:  .5s ease-in-out;
}
.right {
  right: 8%;
}

.left {
  left: 8%;
}
.arrow:hover{
    background-color: #4b483f;
    color: var(--main-color);
}
 .slide {
  opacity: 0;
  transition-duration: 1s ease;
  position: relative;
} 

 .active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}  
.img-title{
  position: absolute;
  bottom: 15%;
  left: 43%;
  color: var(--main-color);
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.input-container{
  position: absolute;
  top:80%;
  left: 42%;
}
.input-container li {
  display: inline-block;
  margin-right: .5rem;

}
.input-container li input{
  cursor: pointer;
}
.btn{
  position: absolute;
  bottom: 3.5%;
  left: 41%;
  background-color: var(--main-color);;
  border: none;
  outline: none;
  color: #4b483f;
  padding: .5rem 2.2rem;
  border-radius: 0.2rem;
  box-shadow: 0px 5px 5px -1px rgba(0, 0, 0, 0.75);
  cursor: pointer;

}
.btn:active{
  background-color: #4b483f;
  color: var(--main-color);
  transform: scale(0.94); 
}

 .copyright {
  width: 80%;
  font-size: 1.2rem;
  color: var(--main-color);
  margin: 5% ;
 text-align: end;
  
}
 
/*popUp window*/

.popUpTable {
    width: 30%;
    height: 30vh;
    box-shadow: 5px 5px 15px 5px #000000;
    text-align: center;
    z-index: 999;
    position: absolute;
    top: 15%;
    left: 33%;
    font-weight: 800;
   
}

.ingredientContainer{
  list-style: none;
}
.ingredientContainer li{
  font-size: 1rem;
  color: #382f17;
}
.ingredientContainer p{
  color: rgb(209, 209, 174);
  font-size: 1.7rem;
}

@media (max-width: 900px) {
 
 .container{
  max-width: 640px;
  width: 100%;
  height: 100%;
}
    .popUpTable {   
     width: 45%;
      height: 42vh;
      left: 27%;
  }
  .input-container{
top:90%;
   
  }
  } 

@media (max-width: 550px) {

  .image {
    width: 320px;
    height: 280px;
  }
  .btn{
    left:37%;
  }
     .copyright {
     display: none;
    }
    .popUpTable {
    box-shadow: 1px 3px 3px 2px rgba(0, 0, 0, 0.75);
     width: 53%;
      height: 23vh;
      font-size: 0.7rem;
      top: 10%;
      left: 23.5%;
  }
  .ingredientContainer p{
    font-size: 1.2rem;
  }
   .input-container{
     margin-top:5%;;
    top:100%;
   }
  }

 .carouselInner {
   width: 100%;
   height: 100%;
   background-position: center;
   background-repeat: no-repeat;
   background-size: cover;
   display: flex;
   transition-duration: 1s ease;
   transition-duration: 1s;
   transform: scale(1.08);
 }


